die {
    text-align: center;
}

.die-container {
    position: relative;
    perspective: 1000px;
    width: 200px;
    height: 200px;
    margin: auto;
    cursor: pointer;
}

/*
    Common Die Style
*/
#die {
    position: relative;
    width: 100%;
    height: 100%;
    margin: auto;
    color: #FFFFFF;
}

