forge {
    display: flex;
    flex-direction: row;
    justify-content: center;
    text-align: center;
    margin: 30px auto;

}

#deforge{
    margin: auto 30px;
    width: 100px;
    height: 100px;
    border-radius:10px;
    background-color: #c14343;
    background-image: url(./images/deforge.svg);
    cursor: pointer;
}

#reforge {
    margin: auto 30px;
    width: 100px;
    height: 100px;
    border-radius:10px;
    background-color: #c14343;
    background-image: url(./images/reforge.svg);
    cursor: pointer;
}

#reforge:active {
    background-color:#a93a3a;
}

#deforge:active {
    background-color: #a93a3a;
}

