nav {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    top: 0;
    width: 100%;
    height: 90px;
    background-color: #595959;
    color: #FFFFFF;
    fill: #FFFFFF;
}

#navbar-title {
    padding-left: 10px;
    font-size: 1.2rem;
    font-weight: 500;
    font-family: 'Berlin Sans FB Demi Bold';
}

nav ul {
    display: flex;
    height: 100%;
    list-style: none;
}

nav ul li {
    height: 100%;
    min-width: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

nav ul li:hover {
    background-color: rgba(255, 255, 255, 0.2);
}

img {
    width: 150px;
}